* {
  outline: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  border: 0;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("bgs/bg1.jpg");
  background-color: #5cdb95;
}

.center {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
