.todoInput {
  border-style: solid;
  border-width: 0px 0px 2px 0px;
  border-color: #05386b;
  background-color: transparent;
  font-size: 12px;
  color: #05386b;
}
input::-webkit-input-placeholder {
  color: #05386b;
  font-size: 14px;
}

.todoUl {
  list-style: none;
  color: #05386b;
}

.todoButton {
  border-color: transparent;
  background-color: transparent;
  font-size: 12px;
  text-align: center;
  cursor: pointer;
}
